<template>
  <div id="login_form">           
        <form action="" method="POST" role="form">
            <legend><h1></h1></legend>

            <div class="form-group">
                <label for=""></label>
                <input type="text" class="form-control" id="username" maxlength="18" placeholder="">
            </div>
            <div class="form-group">
                <label for=""></label>
                <input type="password" class="form-control" id="password" placeholder="">
            </div>

            <button type="submit" class="btn btn-primary"></button>
            <button type="reset" class="btn btn-warning"></button>
        </form>
  </div>
</template>

<script>
export default {
  name: 'Login'
}
</script>

<style scoped>
    #login_form {
        text-align: left;
    }
</style>



